const QUOTE_UPDATE_TIME = 5 * 60 * 1000;

class Quote extends Component {
  constructor(props) {
    super(props);
    this.state = {
      quote: ""
    };
    this.getQuoteFromRandomQuote = this.getQuoteFromRandomQuote.bind(this);
  }

  componentDidMount = () => {
    // this.getQuoteFromRandomQuote();
    // setInterval(() => this.getQuoteFromRandomQuote(), 10000);

    this.getQuoteFromComputerQuotes();
    setInterval(() => this.getQuoteFromComputerQuotes(), QUOTE_UPDATE_TIME);
  };

  getQuoteFromRandomQuote() {
    axios("https://talaikis.com/api/quotes/random/")
      .then(data => {
        const { author, quote } = data.data;
        this.setState({ quote: `{${author}} ${quote} @Random Quote` });
      })
      .catch(err => new Error(err));
  }

  getQuoteFromComputerQuotes() {
    let index = Math.floor(Math.random() * computerQuotes.length);
    this.setState({
      quote: computerQuotes[index]
    });
  }

  render() {
    return <div className="quote">{this.state.quote}</div>;
  }
}

export default Quote;
